            :options="['werkplek_1','werkplek_2','werkplek_3','werkplek_4']"
            :required="true"
            class="input-cast"
          ></vue-single-select>
        </div>
        <div class="filter-item">
          <vue-single-select
            inputId="last"
            v-model="filterLast"
            placeholder="Locatie last"
            :options="['last_1','last_2','last_3','last_4']"
