              :name="profile.firstname"
              :lastName="profile.lastname"
              :text="profile.role_name"
              :img="profile.user_image"
              :stars="calcRating(profile.rating)"
              :type="'stars'">
            </Anketa>
          </div>
        </Carousel>
      </div>

      <div class="slider-form">
