        profiles:[
          {
            img:'photo1.png',
            name:'Lionel',
            text:'Grafisch vormgever',
            stars:5
          },
          {
            img:'photo2.png',
            name:'Thijs',
            text:'Copywriter',
            stars:5
          },
          {
            img:'photo3.png',
            name:'Jochem',
            text:'Grafisch vormgever',
            stars:5
          },
          {
            img:'photo1.png',
            name:'Lionel',
            text:'Grafisch vormgever',
            stars:5
          },
          {
            img:'photo2.png',
            name:'Thijs',
            text:'Copywriter',
            stars:5
          },
          {
            img:'photo3.png',
            name:'Jochem',
            text:'Grafisch vormgever',
            stars:5
