        closedAccounts:[
          {
            img:'photo1.png',
            name:'Lionel',
            text:'Grafisch vormgever',
            textBody: 'Beschikbaar vanaf',
            date: '01-01-18'
          },
          {
            img:'photo3.png',
            name:'Jochem',
            text:'Grafisch vormgever',
            textBody: 'Beschikbaar vanaf',
            date: '01-01-18'
          },
        ],
        inConversation: [
          {
            img:'photo2.png',
            name:'Thijs',
            text:'Copywriter',
            stars:5
          },
        ]
      }
  },
  methods: {
    onClickPrice(index) {
      this.selectedPriceIndex = index;
      console.log(this.priceList[index].val);
    },
    calcRating(num){
      let quantity = 1;
      (num == 0 || num == 1) ? quantity : quantity = Math.floor(num/2);
      return quantity;
    },
  },
  computed: {
    ...mapGetters({
      specialistList: 'specialistList/getAllUsers'
    }),
  },
  components: {
    VueSingleSelect,
    Anketa,
    AnketaDisabled,
    AnketaInConversation,
    Carousel
  },
}
