<style scoped lang="scss">
  .slider-form {
    display: none;
  }
p {
margin: 0;
color: #646464;
font-family: GolanoRegular;
font-size: 25px;
font-weight: 400;
line-height: 30.54px;
}
.price-items {
display: flex;
justify-content: center;
width: 100%;
margin-left: 4%;
}
.price-item {
width: 160px;
text-align: center;
padding-top: 2%;
margin-right: 2% ;
cursor: pointer;

img {
  width: 90%;
  margin-bottom: 12%;
}
