  margin-left: 0;
  width: 100%;
}
}

@media screen and (max-width: 410px) {
.price-item {
  img {
    width: 60%;
  }
  h2 {
    font-size: 1.3rem;
  }
  span {
    font-size: 1rem;
  }
}
}
@media screen and (max-width: 320px){
.price-items {
  margin-left: 0;
  margin-bottom: 10%;
}
.filter-item div {
  width: 100%;
}
.anketa-items {
  margin-left: 0;
  padding: 0;
  .anketa-item {
    padding: 0;
  }
}

}


</style>



